"use client";
import PropertyCard from "../PropertyCard/PropertyCard";
import { useProperties } from "@/services/properties";
import { Card } from "@nextui-org/card";
import { useQuery } from "@tanstack/react-query";
import { useState } from "react";
import { Skeleton } from "@nextui-org/skeleton";

export default function PropertiesCardList() {
  const pagSize = 5;

  const properties = useProperties();

  const [currentPage, setCurrentPage] = useState(1);

  const { isPending, isError, data, error } = useQuery({
    queryKey: ["getProperties", currentPage],
    queryFn: () => properties.getAll(6, currentPage, pagSize),
  });

  const handleNextPage = () => setCurrentPage((prev) => prev + 1);
  const handlePreviousPage = () =>
    setCurrentPage((prev) => Math.max(prev - 1, 1));

  console.log(data);

  const loadingSkeleteon = () => {
    return (
      <Card className="w-full space-y-5 p-4" radius="md">
        <Skeleton className="rounded-lg">
          <div className="h-24 rounded-lg bg-default-300"></div>
        </Skeleton>
        <div className="space-y-3">
          <Skeleton className="w-3/5 rounded-lg">
            <div className="h-3 w-3/5 rounded-lg bg-default-200"></div>
          </Skeleton>
          <Skeleton className="w-4/5 rounded-lg">
            <div className="h-3 w-4/5 rounded-lg bg-default-200"></div>
          </Skeleton>
          <Skeleton className="w-2/5 rounded-lg">
            <div className="h-3 w-2/5 rounded-lg bg-default-300"></div>
          </Skeleton>
        </div>
      </Card>
    );
  };

  return (
    <>
      {isPending ? (
        <div className="flex flex-col gap-4">
          {Array.from({ length: pagSize }).map((_, index) => (
            <div key={index}>{loadingSkeleteon()}</div>
          ))}
        </div>
      ) : (
        <div className="flex flex-col gap-4">
          {data.data.properties?.map((property: any) => (
            <PropertyCard
              key={property.id}
              property={property}
              onClick={() => {}}
            />
          ))}
        </div>
      )}
    </>
  );
}
